<template>
  <div class="row">
    <div class="col-md-12">
      <card>
        <div slot="header">
          <h5 class="title">EDIT TENANT</h5>
        </div>
        <form>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-1 text-right">NAME</label>
            <div class="col-md-3">
              <fg-input type="text" :value="tenant.name" v-model="tenant.name"></fg-input>
            </div>
          </div>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-1 text-right">IC</label>
            <div class="col-md-3">
              <fg-input type="text" :value="tenant.ic" v-model="tenant.ic"></fg-input>
            </div>
            <label class="col-md-2 col-form-label text-right">CONTACT</label>
            <div class="col-md-2">
              <fg-input type="text" :value="tenant.contact" v-model="tenant.contact"></fg-input>
            </div>
          </div>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-1 text-right">EMAIL</label>
            <div class="col-md-3">
              <fg-input type="text" :value="tenant.email" v-model="tenant.email"></fg-input>
            </div>
          </div>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-1 text-right">STATUS</label>
            <div class="col-md-5">
              <n-radio class="col-md-1 display-inline" v-model="enabledRadio" label="ACTIVE">ACTIVE</n-radio>
              <n-radio class="col-md-1 display-inline" v-model="enabledRadio" label="MOVED">MOVED</n-radio>
              <n-radio
                class="col-md-1 display-inline"
                v-model="enabledRadio"
                label="SUSPENDED"
              >SUSPENDED</n-radio>
            </div>
          </div>
          <div class="row">
            <div class="col-md-6 offset-md-4">
              <n-button
                class="button"
                type="info"
                @click.native="radio_status(enabledRadio); put(); handleUpdate();"
              >UPDATE</n-button>
              <a class="cancel">Cancel</a>
              <!-- <n-button class="button" type="danger">CANCEL</n-button> -->
            </div>
          </div>
        </form>
      </card>
    </div>
  </div>
</template>
<script>
import { Radio } from "src/components";
import { Switch } from "src/components";
import swal from "sweetalert2";

export default {
  components: {
    [Radio.name]: Radio,
    [Switch.name]: Switch
  },
  data() {
    return {
      id: this.$route.params.id,
      tenant: {
        name: "",
        ic: "",
        email: "",
        contact: "",
        emergencyContact: "",
        emergencyName: "",
        status: "ACTIVE"
      },
      enabledRadio: "MOVED",
      switches: {
        defaultOn: true
      },
      key_data: {
        name: "",
        ic: "",
        email: "",
        contact: "",
        emergencyContact: "",
        emergencyName: "",
        status: ""
      }
    };
  },
  methods: {
    //update the data to database
    put() {
      this.$http.put(
        "https://admindashboard-b2ab9.firebaseio.com/tenant/" +
          this.id +
          ".json",
        this.tenant
      );
    },
    radio_status(er) {
      if (er == "ACTIVE") {
        this.tenant.status = "ACTIVE";
      } else if (er == "MOVED") {
        this.tenant.status = "MOVED";
      } else {
        this.tenant.status = "SUSPENDED";
      }
    },
    handleUpdate() {
      swal({
        title: `You updated ${this.tenant.name}`,
        buttonsStyling: false,
        type: "success",
        confirmButtonClass: "btn btn-success btn-fill"
      });
    }
  },
  created() {
    //get data to show in textboxes
    this.$http
      .get("https://admindashboard-b2ab9.firebaseio.com/tenant.json")
      .then(function(data) {
        return data.json();
      })
      .then(function(data) {
        //pass data to key_data object
        this.key_data = data;
        //set the fields accordingly with the unique id
        this.tenant.name = this.key_data[this.id].name;
        this.tenant.ic = this.key_data[this.id].ic;
        this.tenant.email = this.key_data[this.id].email;
        this.tenant.contact = this.key_data[this.id].contact;
        this.tenant.status = this.key_data[this.id].status;

        //toggle radio button
        if (this.tenant.status == "ACTIVE") {
          this.enabledRadio = "ACTIVE";
        } else if (this.tenant.status == "MOVED") {
          this.enabledRadio = "MOVED";
        } else {
          this.enabledRadio = "SUSPENDED";
        }
      });
  }
};
</script>
<style scoped>
.display-inline {
  display: inline !important;
}
.cancel {
  /* display: inline; */
  margin-left: 20px;
  color: red;
  text-decoration: underline !important;
}
</style>
